	onLoad() {
		//获取手机系统信息
		uni.getSystemInfo({
			success: (res) => {
				//获取可使用屏幕高度 再减去 顶部选项卡的高度，再调用方法转为upx
				let height = res.windowHeight - uni.upx2px(100);
				this.swiperHeight = height;
			}
		})
	},
	methods: {
		tabtap(index) {
			this.tabIndex = index
		},
		//滑动事件
		tabChange(e) {
			this.tabIndex = e.detail.current;
		},
		//上拉加载事件
		loadMore(index) {
			if (this.newsList[index].loadMoreText != "上拉加载更多") {
				return;
			};
			//修改状态
			this.newsList[index].loadMoreText = "加载中...";
			//获取数据
			//ES6箭头函数没有this指向，所以里面的this直接指向Vue
			setTimeout(() => {
				//获取完成
				let obj = {
					titleimg: "../../static/demo/15611049029292019-6-21web3_4.jpg",
